import React from "react"
import { Behavior } from "gatsby-theme-ceteicean/src/components/Behavior"

import "./pb.css"

const Pb = ({teiNode}) => {
  const pb = teiNode
  const n = pb.getAttribute('n') || ''

  if (n) {
    return (
      <Behavior node={teiNode}>
        <span className="Pb">Page {n}</span>
      </Behavior>    
    )
  }
  return null
}

export default Pb
